.compose{
    position: absolute;
    bottom: 0;
    right: 70px;
    min-width: 500px;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
    box-shadow: 0px 0px 8px rgb(154, 154, 231);
    background-color: white;
}
.compose .compose_header{
    display: flex;
    justify-content: space-between;
    background: #333;
    color: #fff;
    align-items: center;
    padding: 7px 15px;
}
.compose_header_left span{
    font-size: 15px;
}
.compose_header_right{
    display: flex;
    align-items: center;
}
.compose_header_left{
    display: flex;
    align-items: center;
}
.compose .compose_header .compose_header_right svg{
    font-size: 18px;
    margin-left: 10px;
    transition: 0.2s linear 0s;
    padding: 3px;
}
/* .compose_header_right svg:nth-child(2) path{
    transform: rotate(45deg);
    transform-origin: center;
} */
.compose .compose_header .compose_header_right svg:hover{
    background: #545454;
    cursor: pointer;
}
.compose_body{
    display: flex;
}
.compose_bodyform{
    display: flex;
    flex-direction: column;
}
.compose_bodyform input, textarea{
    width: 100%;
    height: 40px;
    outline: 0;
    border: 0px;
    box-shadow: inset 0 -1px 0 0 rgb(100 121 143/12%);
    padding: 0px 10px;
}
.compose_bodyform textarea{
    height: 320px;
    margin-top: 10px;
}
.compose .compose_footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 10px;
}
.compose_footer button{
    /* display: flex; */
    
    cursor: pointer;
    border: 0;
    background-color: rgb(5, 110, 196);
    border-radius: 5px;
    padding: 10px;
    
}
.compose .compose_footer svg:hover{
    cursor: pointer;
}

.emailtype{
    display: flex;
    align-items: center;
    border-bottom: 1px solid whitesmoke;
}
.emailtype > .emailtype_option{
    display: flex;
    align-items: center;
    min-width: 230px;
    padding: 0px 24px;
    cursor: pointer;
    /* border-bottom: 1px solid white; */
    /* margin-right: 10px; */
    /* margin-left: 70px; */
    /* margin: auto; */
}
.emailtype > .emailtype_option svg{
    font-size: 20px;
    color: rgb(93, 100, 105);
    margin-right: 15px;
}
.emailtype > .emailtype_option p{
    font-size: 15px;
    /* align-items: center; */

}
.emailtype > .emailtype_option:hover{
    background-color: white;
}
.emailtype_option--active{
    border-bottom: 3px solid rgb(141, 42, 42);
    color: rgb(141, 42, 42);
    font-weight: bold;
}
.emailbody{
    display: flex;
    align-items: center;
    border-bottom: 1px solid whitesmoke;
    padding: 0px 8px;
    height: 40px;
}
.emailbody:hover{
    cursor: pointer;
    box-shadow: 2px 2px 4px gray;
}
.emailbody .emailbody_left{
    display: flex;
    align-items: center;
    min-width: 300px;
    flex: 20% 1;
}
.emailbody .emailbody_left svg{
    font-size: 18px;
    color: rgb(68, 54, 54);
    margin-right: 15px;
}
.emailbody .emailbody_left h4{
    font-size: 15px;
}
.emailbody .emailbody_middle{
    display: flex;
    align-items: center;
    flex: 90% 1;
}
.emailbody_middle .emailbody_middle_msg{
    display: flex;
    width: 90%;
}
.emailbody .emailbody_middle p{
    font-size: 15px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0px 20px;
    max-width: 700px;
}
.emailbody .emailbody_right{
    display: flex;
    align-items: center;
    flex: 10% 1;
    justify-content: flex-end;
}
.emailbody .emailbody_right p{
    font-size: 12px;
    white-space: nowrap;
}

.emaildetail{
    box-shadow: 0px 0px 13px #ccc;
    height: 250%;
}

.emaildetail_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid whitesmoke;
    padding-left: 15px;
}
.emaildetail_header_left{
    display: flex;
    align-items: center;
    
}
.emaildetail_header svg{
    font-size: 18px;
}
.emaildetail_headermidle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid whitesmoke;
    padding: 0px 15px;
}
.emaildetail_headermidle_left{
    display: flex;
    align-items: center;

}
.emaildetail_headermidle_right{
    display: flex;
    align-items: center;
    
}
.emaildetail_headermidle svg{
    font-size: 18px;
}
.emaildetail_headermidle_left h4{
    margin-right: 10px;
    padding-left: 10px;
}
.emaildetail_headermidle_left p{
    font-size: 14px;
}
.emaildetail_headermidle_right{
    font-size: 14px;
}
.emaildetail_body{
    display: flex;
    padding: 0px 15px;
    
}
.header{
    display: grid;
    grid-template-columns: 250px auto 200px;
    height: 50px;
    align-items: center; 
    border-bottom: 1px solid whitesmoke;
    padding: 10px;
}
.header_left{
    display: flex;
    align-items: center;
}
.header_left img{
    border-radius: 20px;
}
.header_middle{
    display: flex;
    flex: 0.7 1;
}
.search_mail{
    background-color: whitesmoke;
    border-radius: 10px;
    display: flex;
    flex: 0.7 1;
}
.header_middle input{
    border: 0;
    background: transparent;
    outline: 0;
    flex: 1 1;
    font-size: 15px;
    letter-spacing: 1px;
}
.header_right{
    display: flex;
    align-items: center;
}
.header_right .MuiAvatar-root{
    height: 30px;
    width: 30px;
    border-radius: 300px;
}
.sidebar{
    display: flex;
    flex-direction: column;
}
.compose_btn{
    padding: 10px 20px !important;
    border-radius: 40px !important;
    box-shadow: 2px 2px 3px #ccc;
    margin-top: 20px !important;
    margin-bottom: 20px !important;
    margin-left: 10px !important;
    max-width: 150px;
}
.sidebaropt{
    display: flex;
    align-items: center;
    height: 35px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 0px 25px;
    margin-right: 20px;
}
.sidebaropt svg{
    color: rgb(94, 94, 94);
    font-size: 20px;
    width: 20px;
}

.sidebaropt h4{
    font-size: 14px;
    flex: 1 1;
    margin-left: 10px;
    font-weight: normal;
}

.sidebaropt p{
    opacity: 0;
    color: rgb(94, 94, 94);
    font-size: 12px;
    
}
.sidebaropt--active{
    background-color: rgb(231, 153, 148);
}
.sidebaropt:hover{
    cursor: pointer;
    background-color: whitesmoke;
}
.sidebaropt--active h4,
.sidebaropt--active p,
.sidebaropt--active svg{
    color: rgb(107, 4, 4);
    font-weight: bold;
    opacity: 1;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(252, 218, 218);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app_body{
  display: grid;
  grid-template-columns: 250px auto 50px;
}

